<template>
  <div id="year">
    <h1>Gasto anual</h1>
    <div class="loader" v-if="loading">
      <Loader v-if="loading" />
    </div>
    <div class="main-container" :class="{'d-none':loading}">
      <div class="summaryContainer" :class="{'d-none':error}">
        <canvas id="summaryChart" width="100" height="100"></canvas>
        <br>
        <p><strong>Total en el a침o:</strong> ${{this.yearTotal}}</p>
      </div>      
      <br />
      <div class="alert alert-info" v-if="info">{{ this.info }}</div>
      <br />
      <div class="alert alert-danger" v-if="error">{{ this.error }}</div>
    </div>
  </div>
</template>

<script>
import Chart from "chart.js";
import Loader from "../components/Loader";
import { apiEndPoint } from "../settings/settings.js"

export default {
  name: "Year",
  methods: {
    fetchData() {
      var url = apiEndPoint + "getYearData";
      fetch(url)
        .then(response => {
          if(response.ok)
            return response.json()
          else if(response.status === 404){
            throw Error("No se encontr칩 datos para este a침o.")
          }else{
            throw Error(response.statusText);
          }
        })
        .then(success => {          
          this.data.datasets[0].data = success.chartData;
          this.yearTotal = success.totalValue;
        })
        .catch(error => {
          this.error = error;
        })
        .finally(() => {
          this.loading = false;
        });
        if(this.myChart)
            this.myChart.update();
    }
  },  
  mounted() {    
    var ctx = document.getElementById("summaryChart");
    this.myChart = new Chart(ctx, {
      type: "line",
      data: this.data,
      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true
              }
            }
          ]
        },
        responsive: true,
        maintainAspectRatio: false
      }
    });
    this.fetchData();
  },
  data() {
    return {
      loading: true,
      error: null,
      info: null,
      myChart: null,
      data: {
        labels: [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre"
        ],
        datasets: [
          {
            label: "Gasto Mensual",
            data: [NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN],
            borderWidth: 1,
            borderColor: "rgba(255,0,0,0.9)",
            lineTension:0,
            spanGaps:true,
            fill: false
          }
        ]
      },
      yearTotal : 0
    };
  },
  components: {
    Loader
  }
};
</script>

<style scoped>
.summaryContainer
{
  height:30vw;
}
</style>